ber\", -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto, \"PingFang SC\", \"Hiragino Sans GB\", \"Microsoft YaHei\", \"Helvetica Neue\", Helvetica, Arial, sans-serif;\n@code-family            : Consolas, Menlo, Courier, monospace;\n@heading-color          : fade(#000, 85%);\n@text-color             : fade(#000, 65%);\n@text-color-secondary   : fade(#000, 43%);\n@disabled-color         : fade(#000, 25%);\n@heading-color-dark     : fade(#fff, 97%);\n@text-color-dark        : fade(#fff, 91%);\n@text-color-secondary-dark: fade(#fff, 67%);\n@disabled-color-dark    : fade(#fff, 35%);\n@font-size-base         : 12px;\n@font-size-lg           : @font-size-base + 2px;\n@line-height-base       : 1.5;\n@border-radius-base     : 4px;\n@border-radius-sm       : 2px;\n\n// ICONFONT\n@iconfont-css-prefix    : anticon;\n@icon-url               : \"https://at.alicdn.com/t/font_0qcp222wvwijm7vi\";\n\n// LINK\n@link-color             : @primary-color;\n@link-hover-color       : @primary-5;\n@link-active-color      : @primary-7;\n@link-hover-decoration  : none;\n\n// Animation\n@ease-out            : cubic-bezier(0.215, 0.61, 0.355, 1);\n@ease-in             : cubic-bezier(0.55, 0.055, 0.675, 0.19);\n@ease-in-out         : cubic-bezier(0.645, 0.045, 0.355, 1);\n@ease-out-back       : cubic-bezier(0.12, 0.4, 0.29, 1.46);\n@ease-in-back        : cubic-bezier(0.71, -0.46, 0.88, 0.6);\n@ease-in-out-back    : cubic-bezier(0.71, -0.46, 0.29, 1.46);\n@ease-out-circ       : cubic-bezier(0.08, 0.82, 0.17, 1);\n@ease-in-circ        : cubic-bezier(0.6, 0.04, 0.98, 0.34);\n@ease-in-out-circ    : cubic-bezier(0.78, 0.14, 0.15, 0.86);\n@ease-out-quint      : cubic-bezier(0.23, 1, 0.32, 1);\n@ease-in-quint       : cubic-bezier(0.755, 0.05, 0.855, 0.06);\n@ease-in-out-quint   : cubic-bezier(0.86, 0, 0.07, 1);\n\n// Border color\n@border-color-base      : #d9d9d9;        // base border outline a component\n@border-color-split     : #e9e9e9;        // split border inside a component\n@border-width-base      : 1px;            // width of the border for a component\n@border-style-base      : solid;          // style of a components border\n\n// Outline\n@outline-blur-size      : 0;\n@outline-width          : 2px;\n@outline-color          : @primary-color;\n\n// Background color\n@background-color-base  : #f7f7f7;        // basic gray background\n\n// Shadow\n@shadow-color           : rgba(0, 0, 0, .2);\n@box-shadow-base        : @shadow-1-down;\n@shadow-1-up            : 0 -1px 6px @shadow-color;\n@shadow-1-down          : 0 1px 6px @shadow-color;\n@shadow-1-left          : -1px 0 6px @shadow-color;\n@shadow-1-right         : 1px 0 6px @shadow-color;\n@shadow-2               : 0 2px 8px @shadow-color;\n\n// Buttons\n@btn-font-weight        : 500;\n@btn-border-radius-base : @border-radius-base;\n@btn-border-radius-sm   : @border-radius-base;\n\n@btn-primary-color      : #fff;\n@btn-primary-bg         : @primary-color;\n\n@btn-default-color      : @text-color;\n@btn-default-bg         : #fff;\n@btn-default-border     : @border-color-base;\n\n@btn-danger-color      : #fff;\n@btn-danger-bg         : @red-6;\n\n@btn-disable-color      : @disabled-color;\n@btn-disable-bg         : @background-color-base;\n@btn-disable-border     : @border-color-base;\n\n@btn-padding-base       : 4px 15px;\n@btn-font-size-lg       : @font-size-lg;\n@btn-padding-lg         : 4px 15px 5px 15px;\n@btn-padding-sm         : 1px 7px;\n\n@btn-circle-size        : 28px;\n@btn-circle-size-lg     : 32px;\n@btn-circle-size-sm     : 22px;\n\n@btn-group-border       : @primary-7;\n\n// Media queries breakpoints\n// Extra small screen / phone\n@screen-xs              : 480px;\n@screen-xs-min          : @screen-xs;\n\n// Small screen / tablet\n@screen-sm              : 768px;\n@screen-sm-min          : @screen-sm;\n\n// Medium screen / desktop\n@screen-md              : 992px;\n@screen-md-min          : @screen-md;\n\n// Large screen / wide desktop\n@screen-lg              : 1200px;\n@screen-lg-mi